@import url("https://fonts.googleapis.com/css2?family=Germania+One&display=swap");

* {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
  background-color: #f1f6f9;
  color: #343a40;
}

/* -- Home page -- */
.home {
  width: 100%;
  height: 100%;

  .background {
    width: 100%;
    height: 100%;
    background-image: url("../assets/background.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
    position: fixed;
    left: 0;
    right: 0;
    z-index: 1;
    display: block;
  }

  .title-container {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 9999;
    display: flex;

    a {
      color: #bb2205;
      text-decoration: none;
      background-color: rgba(0, 0, 0, 0.7);
      padding: 60px 100px;
      margin: auto;
      max-width: 900px;
      cursor: pointer;
      transition: background-color 0.4s ease;
      border-radius: 0;
      border: 4px solid #bb2205;

      @media (max-width: 576px) {
        max-width: 900px;
        padding: 50px 80px;
      }

      @media (max-width: 390px) {
        max-width: 900px;
        padding: 40px 40px;
      }

      &:hover {
        background-color: rgba(0, 0, 0, 0.8);
      }

      h1 {
        color: #bb2205;
        font-family: "Germania One", cursive;
        font-size: 3rem;
        word-wrap: nowrap;

        @media (max-width: 576px) {
          font-size: 2rem;
        }

        @media (max-width: 390px) {
          font-size: 1.6rem;
        }

        &:hover {
          text-decoration: none;
          color: #bb2205;
        }
      }
    }
  }
}

/* -- Shop page -- */

/* Nav bar*/

.navbar {
  .navbar-brand {
    font-family: "Germania One", cursive;
    color: #bb2205 !important;
    font-size: 1.8rem;
    text-shadow: 2px 2px 0px black;
    letter-spacing: 1px;

    @media (max-width: 390px) {
      font-size: 1.6rem;
    }
  }

  .nav-item {
    font-size: 1rem;
  }

  #toggle-btn {
    align-self: right;
  }

  .fas {
    font-size: 1.6rem;
  }

  .dropdown-menu {
    .dropdown-item:focus {
      background-color: #bb2205;
    }
    .active {
      font-weight: bold;
      letter-spacing: 1px;
      color: white;
      letter-spacing: 1px;
      background-color: #bb2205;
    }
  }
}

// Main shop page
.shop {
  padding-top: 120px; // For fixed navbar which has ~50px height

  .card {
    background-color: #f1f6f9 !important;
    font-family: Arial, Helvetica, sans-serif !important;

    .price {
      font-weight: bold;
    }

    .buy {
      text-align: right;

      .btn {
        color: #f1f6f9 !important;
        letter-spacing: 1px;
        font-weight: bold;
      }
    }
  }
}

//  Welcomin page
.carousel-inner {
  .carousel-item > img {
    max-width: 640px;
    max-height: 640px;
  }
}
